import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import styles from '../../styles/dashboard.module.css';

export default function Login() {
    const [loginUsername, setLoginUsername] = useState('');
    const [loginPassword, setLoginPassword] = useState('');
    const [usernameError, setUsernameError] = useState('');
    const [passwordError, setPasswordError] = useState('');
    const [loginError, setLoginError] = useState('');

    const validateForm = () => {
        let isValid = true;

        if (!loginUsername.trim()) {
            setUsernameError('Username is required');
            isValid = false;
        } else {
            setUsernameError('');
        }

        if (!loginPassword.trim()) {
            setPasswordError('Password is required');
            isValid = false;
        } else {
            setPasswordError('');
        }

        return isValid;
    };

    //const navigate = useNavigate();

    const handleLogin = () => {
        if (validateForm()) {
            axios({
                method: 'post',
                data: {
                    username: loginUsername,
                    password: loginPassword,
                },
                withCredentials: true,
                url: 'https://bgappia2.azurewebsites.net/login',
            })
                .then((response) => {
                    //navigate('/dashboard');
                    window.location.href = '/dashboard';
                    localStorage.setItem('authenticated', 'true');
                    console.log('Login successful!', response.data);
                })
                .catch((error) => {
                    setLoginError('Login failed. Please check username and password.');
                    console.error('Login failed:', error);
                });
        }
    };

    return (

        <body class=" flex justify-center h-screen mt-11">
            <div class="bg-white p-8 rounded-lg shadow-lg w-96 h-80" >
                <div>
                    <h1 className="mt-2 text-center text-3xl font-extrabold text-gray-900">Login</h1>
                </div>



                <div className="mt-4 rounded-md shadow-sm -space-y-px grid grid-cols-1 gap-2">
                    {loginError && <p className="text-red-500 text-sm mt-2">{loginError}</p>}
                    <div>
                        <input
                            id="username"
                            name="username"
                            type="text"
                            required
                            className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-t-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                            placeholder="Username"
                            onChange={(e) => setLoginUsername(e.target.value)}
                        />
                        <p className="text-red-500 text-xs">{usernameError}</p>
                    </div>
                    <div className="gap-2">
                        <input
                            id="password"
                            name="password"
                            type="password"
                            required
                            className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-b-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                            placeholder="Password"
                            onChange={(e) => setLoginPassword(e.target.value)}
                        />
                        <p className="text-red-500 text-xs">{passwordError}</p>
                    </div>
                </div>
                <div>
                    <button type=""
                        onClick={handleLogin} class="mt-4 w-full flex justify-center bg-indigo-700 hover:bg-gray-600 text-white font-bold py-2 px-4 rounded-full focus:outline-none focus:shadow-outline-blue active:bg-blue-800">
                        Login
                    </button>

                </div>

            </div>
        </body>




    );
}
